// 主要用于解决 0.1+0.2 结果的精度问题导致太长
function stripNumber(number) {
    if (typeof number === 'number' && !Number.isInteger(number)) {
        return parseFloat(number.toPrecision(16));
    }
    else {
        return number;
    }
}

export { stripNumber };
